class _AdminHomepage extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      data_list: [],
      data: [],
      searchText: "",
    };
  }

  componentDidMount() {
    let allData: DataListItem[] = [];
    let allApplications: ApplicationsInterface[] = [];

    for (const dataItem of data) {
      let applicationData: DataListItem[] = [];

      for (const link of dataItem.items) {
        if (FC_Access(this.props.auth, link.access).view) {
          allData.push({ ...link, category: dataItem.title });
          applicationData.push({ ...link, category: dataItem.title });
        }
      }

      // create another data push
      allApplications.push({
        description: dataItem.description,
        icon: dataItem.icon,
        title: dataItem.title,
        items: applicationData,
      } as ApplicationsInterface);
      applicationData = [];
    }

    this.setState({
      data: allApplications,
      data_list: allData,
    });
  }

  render() {
    let resultData: DataListItem[] = search(
      this.state.data_list,
      this.state.searchText,
      {
        title: true,
        description: true,
        keywords: true,
        url: true,
        category: true,
      }
    );
    if (this.props.application.apply?.user_id !== undefined) {
      if (this.props.application.apply.user_id === "waiting") {
        if (this.props.application.apply.selectedPosition !== null) {
          if (
            FC_Access(this.props.auth, UserAccessList.MY_APPLICATION).create
          ) {
            return <Redirect to="/apply" />;
          }
        }
      }
    }
    return (
      <div className="-ml-4">
        <div className="p-3 bg-hero text-blue-100 mx-2 mt-2 rounded-lg">
          <h2 className="text-3xl font-extrabold text-center">System Apps</h2>
          <h6 className="text-center">List of system features/menus</h6>

          <div className="m-1 max-w-lg mx-auto rounded-md shadow-md border border-blue-100 bg-transparent p-1 pl-2 flex ">
            <label htmlFor="search-text" className="inline-block text-2xl">
              <MdSearch className="inline-block text-white" />
            </label>
            <input
              type="search"
              className="outline-none bg-transparent flex-1 pl-1 text-white"
              placeholder="Application, reports, candidates..."
              id="search-text"
              onChange={(e) => this.setState({ searchText: e.target.value })}
            />
          </div>
        </div>

        <div className="mt-5 mx-3 animate__animated animate__fadeIn">
          {this.state.searchText.length >= 3 ? (
            <div>
              <div>
                <div className="p-2 bg-gray-500 text-white mt-4 rounded flex items-center">
                  <h2>{resultData.length}</h2>
                  <h2 className="flex-1 pl-1">Results</h2>
                </div>
              </div>

              {resultData.length <= 0 ? (
                <div className="p-6 text-center mt-3 animate__animated animate__fadeIn">
                  <h1 className="text-3xl text-gray-500 font-bold">
                    No results found
                  </h1>
                  <h3 className="text-gray-500">Try using other words</h3>
                </div>
              ) : (
                <div className="grid md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-2 mt-2 ">
                  {resultData.map((link: DataListItem, key) => (
                    <AppItem key={key} link={link} show_category={true} />
                  ))}
                </div>
              )}
            </div>
          ) : this.state.data.length <= 0 ? (
            <div>
              <div className="p-2 bg-gray-500 text-white mt-4 rounded flex items-center">
                <h2>0</h2>
                <h2 className="flex-1 pl-1">Results</h2>
              </div>
            </div>
          ) : (
            this.state.data.map((itm, key_parent) => {
              let ParentIcon = itm.icon;
              return (
                <Fragment key={key_parent}>
                  {itm.items.length > 0 && (
                    <div>
                      <div className="p-2 bg-gray-500 text-white mt-4 rounded flex items-center">
                        <ParentIcon className="pr-3 text-xl " />{" "}
                        <h2 className="flex-1 pl-1">{itm.title}</h2>
                      </div>

                      <div className="grid md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-2 mt-2 ">
                        {itm.items.map((link, key) => (
                          <AppItem
                            key={key}
                            link={link}
                            show_category={false}
                          />
                        ))}
                      </div>
                    </div>
                  )}{" "}
                </Fragment>
              );
            })
          )}
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  application,
}: StoreState): { auth: Auth; application: applicationStoreInterface } => {
  return { auth: auth, application: application };
};

export const AdminHomepage = connect(mapStateToProps, {})(_AdminHomepage);
