import React, { FC, ReactElement } from "react";
import { Link } from "react-router-dom";
import { DataListItem } from "./AdminHomePage";

type AppItemProps = {
  link: DataListItem;
  show_category: boolean;
};

const AppItem: FC<AppItemProps> = ({ link, show_category }): ReactElement => {
  let Icon = link.icon;
  return (
    <Link
      to={link.url}
      className="p-2 bg-white shadow rounded flex hover:bg-blue-100 cursor-pointer"
    >
      <section>
        <Icon className="text-4xl mt-1 text-gray-500" />
      </section>
      <section className="flex-1 pl-1">
        {show_category === true && (
          <h4 className="text-sm text-blue-700">{link.category}</h4>
        )}
        <h3 className="font-bold text-xl">{link.title}</h3>
        <p className="text-sm text-gray-400">{link.description}</p>
      </section>
    </Link>
  );
};
export default AppItem;
